.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #84d9bc;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 1.6rem;
  line-height: 2;
}
@media (min-width: 768px) {
  p {
    font-size: 1.8rem;
  }
}
@media (min-width: 1024px) {
  p {
    font-size: 2rem;
  }
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h3 {
  font-size: 2.8rem;
  text-align: center;
  text-transform: uppercase;
}

ul {
  padding: 0;
  list-style: none;
}

.section {
  padding: 8rem 0;
}
@media (min-width: 768px) {
  .section {
    padding: 10rem 0;
  }
}

.site-header {
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url("../../build/img/header.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 100% 30%;
  padding: 5rem 0;
}
.site-header .logo {
  max-width: 20rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .site-header .logo {
    margin: 0;
  }
}
.site-header .content-header {
  padding: 10rem 0;
}
@media (min-width: 768px) {
  .site-header .content-header {
    padding: 15rem 0;
  }
}
.site-header .content-header .heading-header {
  color: #FFFFFF;
  font-size: 3rem;
}
@media (min-width: 768px) {
  .site-header .content-header .heading-header {
    font-size: 5rem;
    max-width: 50%;
  }
}
@media (min-width: 1024px) {
  .site-header .content-header .heading-header {
    font-size: 6rem;
    max-width: 40%;
  }
}

.button-primary, .button-plan {
  background-color: #84d9bc;
  font-size: 1.8rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #313c4e;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.button-primary:hover, .button-plan:hover {
  cursor: pointer;
  background-color: #5dcda7;
}
@media (min-width: 768px) {
  .button-primary, .button-plan {
    width: auto;
    display: inline-block;
  }
}

.button-plan {
  border: 2px solid #FFFFFF;
  display: block;
}

@media (min-width: 768px) {
  .create-grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.create-grid h2 {
  font-size: 2.5rem;
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
}
@media (min-width: 768px) {
  .create-grid h2 {
    font-size: 3rem;
    margin-top: 0;
  }
}
.create-grid h2::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.4rem 0 0 0;
}

.beneficts {
  background-color: #84d9bc;
}
@media (min-width: 768px) {
  .beneficts .grid-beneficts {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(5, 1fr);
    align-items: center;
  }
}
.beneficts .content-beneficts {
  grid-column: 1/4;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .beneficts .content-beneficts {
    margin-bottom: 0;
  }
}
.beneficts .content-beneficts h2 {
  font-size: 2.5rem;
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
}
@media (min-width: 768px) {
  .beneficts .content-beneficts h2 {
    font-size: 3rem;
    margin-top: 0;
  }
}
.beneficts .content-beneficts h2::after {
  content: "";
  background-color: #FFFFFF;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.4rem 0 0 0;
}
.beneficts .image {
  grid-column: 4/-1;
  transition: all 0.4s ease-out;
}
.beneficts .image:hover {
  transform: rotate(8deg) scale(1.1);
}

.stadistics h2 {
  font-size: 2.5rem;
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
}
@media (min-width: 768px) {
  .stadistics h2 {
    font-size: 3rem;
    margin-top: 0;
  }
}
.stadistics h2::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.4rem 0 0 0;
}
.stadistics p {
  margin-bottom: 5rem;
}
@media (min-width: 1024px) {
  .stadistics .image {
    width: 70%;
    margin: 0 auto;
  }
}

.plans {
  background-color: #313c4e;
}
.plans h2 {
  font-size: 2.5rem;
  display: inline-block;
  margin-top: 5rem;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .plans h2 {
    font-size: 3rem;
    margin-top: 0;
  }
}
.plans h2::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.4rem 0 0 0;
}
.plans .description {
  color: #FFFFFF;
  margin-bottom: 5rem;
}

.ours-plans {
  display: flex;
  flex-direction: column;
  max-width: 90%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .ours-plans {
    margin-top: 10rem;
    max-width: 80rem;
    flex-direction: row;
  }
}
.ours-plans .plan {
  flex-grow: 1;
  background-color: #FFFFFF;
  padding: 20rem 4rem 4rem 4rem;
  border-bottom: 1px solid #e1e1e1;
  transition: all 0.4s ease-out;
}
@media (min-width: 768px) {
  .ours-plans .plan {
    border-bottom: none;
  }
  .ours-plans .plan:hover {
    transform: scale(1.1);
  }
}
.ours-plans .plan .price {
  font-size: 2rem;
  text-align: center;
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 2rem;
}
.ours-plans .plan .list {
  margin-bottom: 3rem;
}
.ours-plans .plan .list li {
  text-align: center;
  margin-bottom: 2rem;
}
.ours-plans .plan .list li:last-of-type {
  margin-bottom: 0;
}
.ours-plans .plan h3 {
  margin-top: 1rem 0 2rem 0;
}
.ours-plans .plan.initial {
  background-image: url("../../build/img/icono_inicio.svg");
  background-repeat: no-repeat;
  background-position: 50% 5rem;
  background-size: 10rem;
}
.ours-plans .plan.bussiness {
  background-color: #84d9bc;
  order: -1;
  background-image: url("../../build/img/icono_negocio.svg");
  background-repeat: no-repeat;
  background-position: 50% 5rem;
  background-size: 10rem;
}
@media (min-width: 768px) {
  .ours-plans .plan.bussiness {
    order: unset;
    margin: -5rem 0;
    border-radius: 2rem;
    background-position: 50% 10rem;
    padding-top: 25rem;
    z-index: 100;
  }
}
.ours-plans .plan.pro {
  background-image: url("../../build/img/icono_pro.svg");
  background-repeat: no-repeat;
  background-position: 50% 5rem;
  background-size: 10rem;
}

.testimonials .image img {
  width: 60%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .testimonials {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(5, 1fr);
    align-items: center;
  }
  .testimonials .image {
    grid-column: 1/3;
  }
  .testimonials .image img {
    width: 80%;
    margin: 0 auto;
  }
  .testimonials .content {
    grid-column: 3/-1;
  }
}
.testimonials h2 {
  font-size: 2.5rem;
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
}
@media (min-width: 768px) {
  .testimonials h2 {
    font-size: 3rem;
    margin-top: 0;
  }
}
.testimonials h2::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.4rem 0 0 0;
}

.content .testimonial {
  background-color: #84d9bc;
  border: 0.5rem solid #313c4e;
  margin-bottom: 4rem;
  padding: 2rem;
  border-radius: 1rem;
  position: relative;
}
@media (min-width: 1024px) {
  .content .testimonial {
    max-width: 70%;
  }
  .content .testimonial:nth-child(2) {
    margin-left: 10%;
  }
  .content .testimonial:nth-child(3) {
    margin-left: 20%;
  }
  .content .testimonial:nth-child(4) {
    margin-left: 30%;
  }
}
.content .testimonial::before {
  position: absolute;
  content: "";
  display: block;
  width: 5rem;
  height: 5rem;
  background-image: url(../../build/img/comilla.svg);
  background-repeat: no-repeat;
  background-position: center center;
}
.content .testimonial p {
  padding-left: 6rem;
}

.footer {
  background-color: #313c4e;
  color: #FFFFFF;
}
.footer-grid {
  padding: 8rem 0;
}
@media (min-width: 768px) {
  .footer-grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.footer .logo {
  max-width: 20rem;
  margin: 0 auto 5rem auto;
}
@media (min-width: 768px) {
  .footer .logo {
    margin-bottom: 0;
  }
}
.footer-widget {
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .footer-widget {
    margin-bottom: 0;
  }
}
.footer-widget h3 {
  color: #84d9bc;
  text-align: left;
}
@media (min-width: 768px) {
  .footer-widget h3 {
    margin-bottom: 3rem;
  }
}
.footer nav a {
  display: block;
  color: #FFFFFF;
  margin-bottom: 1.5rem;
}
.footer nav a:last-of-type {
  margin-bottom: 0;
}
.footer .copyright {
  text-align: center;
  padding: 1rem;
  margin: 0 auto;
  border-top: 1px solid #84d9bc;
  max-width: 120rem;
  font-size: 1.2rem;
  text-transform: uppercase;
}